<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- card -->
    <el-card>
      <el-button type="primary" @click="changeaddcate"></el-button>
      <tree-table
        index-text="#"
        :data="goodscate"
        :columns="columns"
        :selectable="false"
        :expand-type="false"
        show-index
        border
        class="tree-table"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            v-if="scope.row.cat_deleted == false"
            class="el-icon-circle-check"
            style="color: lightgreen"
          ></i>
          <i v-else class="el-icon-circle-close" style="color: red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level == 0" size=":mine"></el-tag>
          <el-tag
            v-else-if="scope.row.cat_level == 1"
            size=":mine"
            type="success"
            ></el-tag
          >
          <el-tag v-else size=":mine" type="warning"></el-tag>
        </template>

        <!--  -->
        <template slot="set" slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="changeeditcate(scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="deletecate(scope.row)"
            ></el-button
          >
        </template>
      </tree-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :currentPage="goodsargument.pagenum"
        layout="total, prev, pager, next, jumper"
        :total="total"
        :pageSize="goodsargument.pagesize"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="isaddcate" width="30%">
      <el-form
        ref="addcateref"
        :model="addcate"
        :rules="addcaterules"
        label-width="80px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addcate.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            v-model="parentcate"
            :options="showcate"
            :props="{
              expandTrigger: 'hover',
              value: 'cat_id',
              label: 'cat_name',
              children: 'children',
            }"
            @change="selectchange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isaddRoles = false"> </el-button>
        <el-button type="primary" @click="addcatehandle()"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="iseditcate" width="30%">
      <el-form
        ref="editcateref"
        :model="editcate"
        :rules="addcaterules"
        label-width="80px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editcate.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="iseditcate = false"> </el-button>
        <el-button type="primary" @click="editcatehandle()"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //   
      goodsargument: {
        pagenum: 1,
        pagesize: 5,
      },
      //   
      total: 0,
      //   
      goodscate: [],
      columns: [
        { title: "", key: "cat_name" },
        {
          title: "",
          type: "template",
          template: "isok",
        },
        {
          title: "",
          type: "template",
          template: "order",
        },
        {
          title: "",
          type: "template",
          template: "set",
        },
      ],
      isaddcate: false,
      addcaterules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, message: "1", trigger: "blur" },
        ],
      },
      //   form
      showcate: {},
      parentcate: [],
      //   
      addcate: {
        cat_pid: 0,
        cat_name: "",
        cat_level: 0,
      },
      iseditcate: false,
      editcate: {},
    };
  },
  created() {
    this.getgoodscate();
  },
  methods: {
    async getgoodscate() {
      const { data: res } = await this.$http.get("categories", {
        params: this.goodsargument,
      });
      if (res.meta.status != 200)
        return this.$message.error("");
      this.goodscate = res.data.result;
      this.total = res.data.total;
    },
    // 
    handleCurrentChange(currentpage) {
      this.goodsargument.pagenum = currentpage;
      this.getgoodscate();
    },
    // 
    async changeaddcate() {
      const { data: res } = await this.$http.get("categories", {
        params: { type: 2 },
      });
      console.log(res);
      if (res.meta.status != 200)
        return this.$message.error("");
      this.showcate = res.data;
      this.isaddcate = true;
    },
    // 
    selectchange() {
      if (this.parentcate.length > 0) {
        this.addcate.cat_pid = this.parentcate[this.parentcate.length - 1];
        this.addcate.cat_level = this.parentcate.length;
      } else {
        this.addcate.cat_pid = 0;
        this.addcate.cat_level = 0;
      }
    },
    // 
    addcatehandle() {
      this.$refs.addcateref.validate(async (res) => {
        if (!res) return;
        const { data: r } = await this.$http.post("categories", this.addcate);
        if (r.meta.status != 201) return this.$message.error("");
        this.$message.success("");
        this.getgoodscate();
        this.isaddcate = false;
      });
    },
    async changeeditcate(scope) {
      const { data: res } = await this.$http.get("categories/" + scope.cat_id);
      console.log(res);
      if (res.meta.status != 200) return this.$message.error("");
      this.editcate = res.data;
      this.iseditcate = true;
    },
    // 
    editcatehandle() {
      this.$refs.editcateref.validate(async (vali) => {
        if (!vali) return;
        const { data: r } = await this.$http.put(
          "categories/" + this.editcate.cat_id,
          { cat_name: this.editcate.cat_name }
        );
        if (r.meta.status != 200) return this.$message.error("");
        this.$message.success("");
        this.getgoodscate();
        this.iseditcate = false;
      });
    },
    // 
    async deletecate(scope) {
      console.log(scope);
      const res = await this.$confirm(
        "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).catch((res) => res);
      if (res != "confirm") return this.$message.info("");
      const { data: r } = await this.$http.delete("categories/" + scope.cat_id);
      console.log(r);
      if (r.meta.status != 200) return this.$message.error("");
      this.$message.success("");
      this.getgoodscate();
    },
  },
};
</script>
<style lang='less' scoped>
.tree-table {
  margin-top: 15px;
}
</style>